import { ExcalidrawElement } from "../../element/types";
import { AppState } from "../../types";
import {
  clearAppStateForLocalStorage,
  getDefaultAppState,
} from "../../appState";
import { clearElementsForLocalStorage } from "../../element";
import { STORAGE_KEYS as APP_STORAGE_KEYS } from "../../constants";
import {  exportToSvg } from "../../scene/export";


//  url , Local Storage 
// url
// fileKey=excalidraw111&viewKey=excalidraw-state123
function getUrlValue(kValue: string) {
   let url = window.location.href;  //
    let reg = /([^?&=]+)=([^?&=]+)/g, obj:any = {}, str = url;
    url.replace(reg, function ():any {
        obj[arguments[1]] = arguments[2];
    })
    for (let keyName in obj) {
        if (keyName == kValue) {
            return obj[keyName];
            // break;
        }
    }
}

// console.log(getUrlValue('fileKey'), getUrlValue('viewKey'));

export const STORAGE_KEYS = {
  LOCAL_STORAGE_ELEMENTS: getUrlValue('fileKey') || "excalidraw",
  LOCAL_STORAGE_APP_STATE: getUrlValue('viewKey') || "excalidraw-state",
  LOCAL_STORAGE_COLLAB: "excalidraw-collab",
  LOCAL_STORAGE_KEY_COLLAB_FORCE_FLAG: "collabLinkForceLoadFlag",
};

export const saveUsernameToLocalStorage = (username: string) => {
  console.log(username, '----')
  try {
    localStorage.setItem(
      STORAGE_KEYS.LOCAL_STORAGE_COLLAB,
      JSON.stringify({ username }),
    );
  } catch (error) {
    // Unable to access window.localStorage
    console.error(error);
  }
};

export const importUsernameFromLocalStorage = (): string | null => {
  try {
    const data = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_COLLAB);
    if (data) {
      return JSON.parse(data).username;
    }
  } catch (error) {
    // Unable to access localStorage
    console.error(error);
  }

  return null;
};

export const saveToLocalStorage = (
  elements: readonly ExcalidrawElement[],
  appState: AppState,
  
) => {
  console.log('---saveToLocalStorage---')
  try {
    let files = JSON.parse(localStorage.getItem('image' + getUrlValue('fileKey')) || '{}');
    // console.log(files);
    // console.log(elements, appState)
    let svg = exportToSvg(elements, {
      exportBackground: appState.exportBackground,
      exportWithDarkMode: appState.exportWithDarkMode,
      viewBackgroundColor: appState.viewBackgroundColor,      
    },files || null);
    svg.then((svg)=>{
      localStorage.setItem('svg' + getUrlValue('fileKey'),svg.outerHTML)
    })

    localStorage.setItem(
      STORAGE_KEYS.LOCAL_STORAGE_ELEMENTS,
      JSON.stringify(clearElementsForLocalStorage(elements)),
    );
    localStorage.setItem(
      STORAGE_KEYS.LOCAL_STORAGE_APP_STATE,
      JSON.stringify(clearAppStateForLocalStorage(appState)),
    );                        
    window.parent.postMessage({
      noteContent:'',
      noteViewContent:'',
      isAutoSaveExcalidraw:true
    }, '*');            
  } catch (error) {
    // Unable to access window.localStorage
    console.error(error);
  }
};

export const importFromLocalStorage = () => {
  let savedElements = null;
  let savedState = null;

  try {
    // local Stirage local 
    savedElements = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_ELEMENTS);
    savedState = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_APP_STATE);
    // console.log(savedElements, savedState)
  } catch (error) {
    // Unable to access localStorage
    console.error(error);
  }

  let elements: ExcalidrawElement[] = [];
  if (savedElements) {
    try {
      elements = clearElementsForLocalStorage(JSON.parse(savedElements));
    } catch (error) {
      console.error(error);
      // Do nothing because elements array is already empty
    }
  }

  let appState = null;
  if (savedState) {
    try {
      appState = {
        ...getDefaultAppState(),
        ...clearAppStateForLocalStorage(
          JSON.parse(savedState) as Partial<AppState>,
        ),
      };
    } catch (error) {
      console.error(error);
      // Do nothing because appState is already null
    }
  }
  return { elements, appState };
};

export const getElementsStorageSize = () => {
  try {
    const elements = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_ELEMENTS);
    const elementsSize = elements ? JSON.stringify(elements).length : 0;
    return elementsSize;
  } catch (error) {
    console.error(error);
    return 0;
  }
};

export const getTotalStorageSize = () => {
  try {
    const appState = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_APP_STATE);
    const collab = localStorage.getItem(STORAGE_KEYS.LOCAL_STORAGE_COLLAB);
    const library = localStorage.getItem(
      APP_STORAGE_KEYS.LOCAL_STORAGE_LIBRARY,
    );

    const appStateSize = appState ? JSON.stringify(appState).length : 0;
    const collabSize = collab ? JSON.stringify(collab).length : 0;
    const librarySize = library ? JSON.stringify(library).length : 0;

    return appStateSize + collabSize + librarySize + getElementsStorageSize();
  } catch (error) {
    console.error(error);
    return 0;
  }
};
